<template>
  <div class="login">

    <div class="container">
      <p>欢迎登录<span>声乐</span>管理系统</p>
      <el-input placeholder="请输入账号"
                type="text"
                v-model="account"></el-input>
      <el-input placeholder="请输入密码"
                type="text"
                v-model="password"
                show-password></el-input>
      <div class="config">
        <el-checkbox v-model="saveAccountState">记住账号</el-checkbox>
        <el-button>忘记密码</el-button>
      </div>
      <el-button type="primary"
                 @click="_login">登录</el-button>
    </div>

  </div>
</template>

<script>
import { ckSetToken, ckGetAccount, ckGetPassword, ckSetAccount, ckSetPassword } from "@/utils/storeUtil/cookieUtil"
import { lsSetSaveAccountSate, lsGetSaveAccountSate } from "@/utils/storeUtil/localStorageUtil"

export default {
  data () {
    return {
      saveAccountState: lsGetSaveAccountSate(),
      account: "",
      password: "",
    }
  },
  methods: {
    _login () {

      var msg = null
      if (!this.account) {
        msg = "请输入账号"
      } else if (!this.password) {
        msg = "请输入密码"
      }
      if (msg) {
        this.$message(msg)
        return
      }
      ckSetToken(this.password)
      lsSetSaveAccountSate(this.saveAccountState)
      if (this.saveAccountState) {
        ckSetAccount(this.account)
        ckSetPassword(this.password)
      } else {
        ckSetAccount("")
        ckSetPassword("")
      }
      this.$router.push({ path: "/home" })
    },
  },
  created () {
    console.log(this.saveAccountState)
    this.account = this.saveAccountState ? ckGetAccount() : ""
    this.password = this.saveAccountState ? ckGetPassword() : ""
  }
}
</script>

